import {
  Button,
  PanelBody,
  RangeControl,
  SelectControl,
  TextControl,
  ToggleControl,
} from '@wordpress/components'
import { InspectorControls } from '@wordpress/block-editor'
import { Component } from '@wordpress/element'

export default class Inspector extends Component {
  constructor (props) {
    super(props)
    this.state = { data: { type: '', label: '' } }
  }

  render () {
    const {
      attributes: {
        fields,
        isModal,
        nameModalButton,
        classNameModalButton,
        classNameButton,
        nameButton
      },
      setAttributes,
    } = this.props

    const uuidv4 = () => {
      return ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c => (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16),)
    }

    const data = this.state.data
    const setData = (params) => this.setState({ data: params })

    return (
      <InspectorControls>
        <PanelBody title=" " initialOpen={true}>
          <SelectControl
            label=" "
            value={data.type}
            options={[
              { value: null, label: '  ' },
              { value: 'text', label: ' ' },
              { value: 'hidden', label: ' ' },
              { value: 'textarea', label: ' ' },
              { value: 'email', label: '  ' },
              { value: 'tel', label: '  ' },
              { value: 'number', label: ' ' },
            ]}
            onChange={(value) => setData({ 'type': value, 'label': '' })}
          />
          <TextControl
            value={data.label}
            label=""
            onChange={(value) => setData({ 'type': data.type, 'label': value })}
          />
          {/*{(data.type === 'select' || data.type === 'radio') && (
              <SelectControl
                label="  "
                options={[
                  { value: null, label: '  ' },
                  { value: '1', label: '1' },
                  { value: '2', label: '2' },
                ]}
                onChange={(value) => {
                  setdata({ 'type': data.type, 'options': value })
                }}
              />
            )}*/}

          <Button
            className={'is-primary'}
            onClick={() => {
              const typeName = (type) => {
                const t = {
                  'text': ' ',
                  'textarea': ' ',
                  'hidden': ' ',
                  'tel': '  ',
                  'email': '  ',
                  'number': ' ',
                }
                return t[type]
              }

              if (data.type === undefined || data.type === '') {
                alert('  ')
              } else {
                setAttributes({
                  fields: ([
                    ...fields, {
                      label: data.label === '' ? typeName(data.type) : data.label,
                      type: data.type,
                      name: uuidv4(),
                      description: '',
                      placeholder: '',
                      width: '12',
                      validation: {
                        /*max: false,
                        min: false,
                        pattern: false,
                        maxLength: false,
                        minLength: false,*/
                        required: false,
                      },
                      data: {
                        'type': '',
                        'filter_name': 'isvek_plugin_field_filter'
                      }
                    }]),
                })

                setData({ 'type': '', 'label': '' })
              }
            }}
          ></Button>
        </PanelBody>
        <PanelBody title="  " initialOpen={true}>
          <ToggleControl
            label="  ?"
            checked={isModal}
            onChange={(value) => setAttributes({ isModal: value })}
          />

          {isModal && (
            <>
              <TextControl
                value={nameModalButton}
                label="   "
                onChange={(value) => setAttributes({ nameModalButton: value })}
              />
              <TextControl
                value={classNameModalButton}
                label="   "
                onChange={(value) => setAttributes({ classNameModalButton: value })}
              />
            </>
          )}
          <TextControl
            value={nameButton}
            label="  "
            onChange={(value) => setAttributes({ nameButton: value })}
          />
          <TextControl
            value={classNameButton}
            label="  "
            onChange={(value) => setAttributes({ classNameButton: value })}
          />
        </PanelBody>
      </InspectorControls>
    )
  }
}
